body {
  color: #FFF;
  background-color: #01082d;
  background: #e9275b;
  /* Old browsers */
  /* FF3.6-15 */
  /* Chrome10-25,Safari5.1-6 */
  background: -webkit-gradient(linear, left top, left bottom, from(#e9275b), to(#ffff66));
  background: linear-gradient(to bottom, #e9275b 0%, #ffff66 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e9275b', endColorstr='#ffff66',GradientType=0 );
  /* IE6-9 */
  font-size: 14px;
  font-family: "Montserrat", sans-serif;
  margin: 0; }

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.page-content {
  min-height: calc(100vh - 125px);
  padding: 25px;
  padding-top: 100px;
  margin: 0 auto;
  background: #01082d;
  display: flex;
  justify-content: center;
  z-index: 100;
  width: calc(100vw - 50px);
  position: absolute; }
  .page-content .content-wrapper {
    max-width: 1000px; }
  @media screen and (max-width: 800px) {
    .page-content {
      padding: 0px;
      width: 100vw; } }

